import React, {
	forwardRef,
	useEffect,
	useLayoutEffect,
	useRef,
	useState,
} from "react";

import LinkButton from "../Button/LinkButton";
import { Icon } from "../Icon/Icon";
import Label from "../Label/Label";
import { colors } from "../styleguide";
import Tag from "../Tag/Tag";
import Tooltip from "../Tooltip/Tooltip";
import { getColorWithOpacity } from "../utils";

import {
	ComponentWrapper,
	InputWrapper,
	LeftIconWrapper,
	LinkButtonWrapper,
	Prefix,
	RightIconWrapper,
	StyledInput,
	Suffix,
	TrailingLinkButton,
} from "./Input.styles";
import { InputProps } from "./Input.types";
import { buildAriaLabel, buildRequirements } from "./InputRequirements";

const Input = forwardRef<HTMLInputElement, InputProps>(({
	dataQA,
	id,
	type = "text",
	label,
	required = false,
	disabled,
	requirements,
	rightIcon,
	rightTooltipText,
	rightTooltipPosition,
	leftIcon,
	topButtonLinkText,
	topButtonLinkAction,
	onChange = () => { },
	validation,
	mt,
	ml,
	mr,
	mb,
	m,
	className,
	prefix,
	suffix,
	trailingButtonLeftIcon,
	trailingButtonText,
	trailingButtonAction = () => { },
	autoComplete = "off",
	isTagsInput = false,
	value,
	"data-1p-ignore": data1pIgnore = "true",
	...props
}, ref) => {
	const getIconColor = () => {
		if (validation === "valid") return colors.green;
		if (validation === "invalid") return colors.destructive;
		return `${getColorWithOpacity(colors.black, 54)}`;
	};

	const prefixRef = useRef(document.createElement("span"));
	const suffixRef = useRef(document.createElement("span"));
	const trailingButtonRef = useRef<HTMLButtonElement>(document.createElement("button"));
	const [prefixWidth, setPrefixWidth] = useState(0);
	const [trailingButtonWidth, setTrailingButtonWidth] = useState(0);

	useLayoutEffect(() => {
		setPrefixWidth(prefixRef.current.offsetWidth);
		setTrailingButtonWidth(trailingButtonRef.current?.offsetWidth);
	}, [trailingButtonWidth]);

	const [tags, setTags] = useState<string[]>([]);

	useEffect(() => {
		if (Array.isArray(value) && isTagsInput) {
			setTags([...value]);
		}
	}, [value, isTagsInput]);

	const updateTags = (newTags: string[]) => {
		if (isTagsInput) {
			setTags(newTags);
			onChange({ tags: newTags });
		}
	};

	const removeTag = (index: number) => {
		const copyTags = [...tags];
		copyTags.splice(index, 1);
		updateTags(copyTags);
	};

	const handleKeyDown = (event: React.KeyboardEvent<HTMLInputElement>) => {
		event.stopPropagation();

		const target = event.target as HTMLInputElement;
		const text = target.value;

		if (!text
			&& tags?.length
			&& event.key === "Backspace"
		) {
			removeTag(-1);
			target.value = "";
		}

		const separators = ["Enter", ","];

		if (text && separators.includes(event.key) && tags) {
			event.preventDefault();
			updateTags([...tags, ...text.split(",")]);
			target.value = "";
		}
	};

	const handleOnChange = (event?: React.ChangeEvent<HTMLInputElement>) => {
		if (isTagsInput) {
			return;
		}

		onChange({ event });
	};

	return (
		<ComponentWrapper
			mt={mt}
			mb={mb}
			ml={ml}
			mr={mr}
			m={m}
			data-qa={dataQA}
			className={className}
			aria-required={required}
		>
			{label
				&& (
					<Label
						dataQA={`${dataQA}-label`}
						isInvalid={validation === "invalid"}
						htmlFor={id}
						id={`${dataQA}-label`}
						isRequired={required}
					>
						{label}
					</Label>
				)}
			{topButtonLinkText && (
				<LinkButtonWrapper>
					{typeof topButtonLinkText === "string" ?
						<LinkButton onClick={topButtonLinkAction} dataQA={`${dataQA}-buttonLink`} buttonText={topButtonLinkText} />
						: topButtonLinkText
					}
				</LinkButtonWrapper>
			)}
			<InputWrapper validation={validation} disabled={disabled}>
				{leftIcon && (
					<LeftIconWrapper data-qa={`${dataQA}-left-icon`}>
						<Icon name={leftIcon} color={getIconColor()} size={20} strokeWidth={2} />
					</LeftIconWrapper>
				)}
				{prefix
					&& (
						<Prefix
							ref={prefixRef}
							dataQA={`${dataQA}-prefix`}
							variant="bodyCopyBold"
							component="span"
							leftIcon={leftIcon}
							validation={validation}
							color={validation === "invalid" ? colors.red : colors.black}
						>
							{prefix}
						</Prefix>
					)}
				{isTagsInput && tags && (
					tags.map((tag, index) => (
						<Tag
							key={index}
							dataQA={`${tag}-input-tag`}
							text={tag}
							onDelete={() => removeTag(index)}
							isDisabled={disabled}
						/>
					))
				)}
				{prefixWidth > -1 && trailingButtonWidth > -1 && (
					<StyledInput
						ref={ref}
						{...props}
						required={required}
						value={isTagsInput ? undefined : value}
						aria-labelledby={buildAriaLabel(label, requirements, dataQA)}
						data-qa={`${dataQA}-input`}
						id={id}
						key={id}
						validation={validation}
						rightIcon={rightIcon}
						type={type}
						trailingButtonWidth={trailingButtonWidth}
						aria-invalid={validation === "invalid"}
						autoComplete={autoComplete}
						onChange={(e) => handleOnChange(e)}
						disabled={disabled}
						data-1p-ignore={data1pIgnore}
						{...(isTagsInput && {
							onKeyDown: handleKeyDown,
						})}
					/>
				)}
				{suffix
					&& (
						<Suffix
							ref={suffixRef}
							dataQA={`${dataQA}-suffix`}
							variant="bodyCopy"
							component="span"
							color={validation === "invalid" ? colors.red : `${getColorWithOpacity(colors.black, 66)}`}
							trailingButtonWidth={trailingButtonWidth}
						>
							{suffix}
						</Suffix>
					)}
				{rightIcon && (validation !== "valid") && (
					<RightIconWrapper
						data-qa={`${dataQA}-right-icon`}
						rightTooltipText={rightTooltipText}
						trailingButtonWidth={trailingButtonWidth}
					>
						{rightTooltipText ? (
							<Tooltip dataQA={`${dataQA}-right-tooltip`} position={rightTooltipPosition} text={rightTooltipText}>
								<Icon name={rightIcon} color={getIconColor()} size={20} strokeWidth={2} />
							</Tooltip>
						) : (
							<Icon name={rightIcon} color={getIconColor()} size={20} strokeWidth={2} />
						)}
					</RightIconWrapper>
				)}
				{validation && validation === "valid" && (
					<RightIconWrapper data-qa={`${dataQA}-check-icon`} >
						<Tooltip dataQA={`${dataQA}-check-tooltip`} position={rightTooltipPosition} text="validated">
							<Icon name="Check" color={getIconColor()} size={17} strokeWidth={2} />
						</Tooltip>
					</RightIconWrapper>
				)}
				{trailingButtonText && (
					<TrailingLinkButton
						ref={trailingButtonRef}
						dataQA={`${dataQA}-trailing-button`}
						leftIcon={trailingButtonLeftIcon}
						onClick={trailingButtonAction}
						disabled={disabled}
						buttonText={trailingButtonText}
					/>
				)}
			</InputWrapper>
			{requirements && buildRequirements(requirements, dataQA, id, validation)}
		</ComponentWrapper>
	);
});
Input.displayName = "Input";
export default Input;
